<template>
  <div class="order">
    <div class="orderItem">
      <div class="orderSn ">
        <span class="sn ">: {{item.sn}}</span>
        <span class="status " :class="'status'+item.status">{{status(item.status)}}</span>
      </div>
      <div class="orderMsg ">
        <div class="msg">
          <div class="ttl-logo">
            <div class="ttl" v-text="item.name"></div>
            <div class="logo"><img :src="baseUrl + item.brand_info.logo"></div>
          </div>
          <div class="dl" v-if="item.relation_info.name && [1,2,3,4,5].filter(i=>i===item.status).length">: {{item.policy_num}}</div>
          <div class="dl">{{item.relation_info.name}}</div>
          <div class="dl"  v-if="item.relation_info.name && [0,7,6,8].filter(i=>i===item.status).length">{{item.create_time}}</div>
          <div class="dl" v-if="item.relation_info.name && [1,2,3,4,5].filter(i=>i===item.status).length">: {{item.policy_effective_date}}{{item.policy_expiration_date}}</div>
        </div>
        <div class="btn-price">

          <div class="price">
            <span class="p">{{item.amount}}</span>
            <span class="i"></span>
          </div>
          <div class="btns">
            <div class="btn baodan" v-if="item.policy_url!==null"></div>
            <div class="btn againPay" v-if="[5,6,7].filter(i=>i===item.status).length" @click='handleRePay(item)'></div>
            <div class="btn toSettle" v-if="[4].filter(i=>i===item.status).length || ([5].filter(i=>i===item.status).length && dateDiff(item.policy_expiration_date))"></div>
            <div class="btn toPay" v-if="item.status===0" @click='handleToPay(item)'></div>
            <div class="btn rePay" v-if="[8].filter(i=>i===item.status).length" @click='handleToPay(item)'></div>
            <div class="btn toDetail" v-if="[0,1,2,3,4,5,6,7,8].filter(i=>i===item.status).length" @click="goDetail(item)"></div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      default () {
        return {}
      },
      required: true
    }

  },
  data () {
    return {
      baseUrl: `${process.env.VUE_APP_RESOURCE_URL}`
    }
  },
  mounted () {
    window.diff = this.dateDiff
  },
  methods: {
    goDetail (item) {
      this.$router.push({
        path: `/myOrder/${item.id}`,
        query: {
          orderId: item.id
        }
      })
    },
    handleRePay (item) { // 
      this.$router.push({
        query: {
          productId: item.product_id,
          orderSn: item.sn
        },
        path: `/product/detail/${item.product_id}`
      })
    },
    handleToPay (item) { //  //
      this.$router.push({
        query: {
          sn: item.sn,
          productId: item.product_id
        },
        path: `/product/orderConfirm`
      })
    },
    dateDiff (odate) {
      if (!odate) return false
      var dateStr = new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate()
      var odate = odate.split(' ')[0]
      let Date1 = new Date(dateStr)
      let Date2 = new Date(odate)
      let iDays = parseInt(Math.abs(Date1 - Date2) / 1000 / 60 / 60 / 24)  // 
      console.log(iDays)
      return iDays <= 200
    },
    status (status) {
      let res
      switch (status) {
        case 0:
          res = ''
          break
        case 1:
          res = ''
          break
        case 2:
          res = ''
          break
        case 3:
          res = ''
          break
        case 4:
          res = ''
          break
        case 5:
          res = ''
          break
        case 6:
          res = ''
          break
        case 7:
          res = ''
          break
        case 8:
          res = ''
          break
      }
      return res
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';

  .order {
    .orderItem {
      background-color: #FFFFFF;
      padding-left:pxToRem(30px);
      margin-bottom:pxToRem(20px);
      &.last-item {
        border-bottom: 0px;
      }
      .orderSn {
        display: flex;
        justify-content: space-between;
        height: pxToRem(84px);
        line-height: pxToRem(84px);
        width: 100%;
        /*margin-left: 20px;*/
        border-bottom: $border-bottom-light-color;
        .sn {
          font-size: pxToRem(30px);
          color: #A5A8A8;
        }
        .status {
          font-size: pxToRem(30px);
          padding-right:pxToRem(30px);
        }
        .status0, .status1,  .status2,.status3, .status5 ,.status6,.status7 ,.status8{
          color: #B6BBBB;
        }
        .status4 {
          color: #1FD8D2;
        }
      }
      .orderMsg {
        .btn-price {
          position: relative;
          display: flex;
          justify-content: space-between;
          height:pxToRem(100px);
          line-height:pxToRem(100px);
          .price {
            text-align: right;
            .p {
              font-size: pxToRem(40px);
              color: $color-active-text;
            }
            .i {
              font-size: pxToRem(24px);
              color: #C5C5C5;
            }
            .p, .i {
              vertical-align: bottom;
            }
          }
          .btns {
            .btn {
              display: inline-block;
              width:pxToRem(150px);
              height:pxToRem(60px);
              line-height: pxToRem(60px);
              margin-right:  pxToRem(20px);
              font-size:  pxToRem(24px);
              border-radius:  pxToRem(34px);
              text-align: center;
              &.toDetail {
                color: #FFFFFF;
                background: #1FD8D2;
                border: 1px solid $color-active-text;
              }
              &.toPay, &.againPay, &.rePay {
                color: #FF4B4B;
                background: #FFFFFF;
                border: 1px solid #FF4B4B;
              }
              &.baodan {
                color: #A5A8A8;
                border: 1px solid #D5D8D8;
                background: #FFFFFF;
              }
              &.toSettle {
                color: #1FD8D2;
                border: 1px solid #1FD8D2;
                background: #FFFFFF;
              }
            }
          }
        }
        .msg {
          overflow: hidden;
          margin-top: pxToRem(30px);
          padding-right: pxToRem(30px);
          position: relative;
          border-bottom: $border-bottom-light-color;
          .ttl-logo{
            display: flex;
            justify-content: space-between;
            margin-bottom:pxToRem(10px);
            .logo {
              width:pxToRem(110px);
              height:pxToRem(60px);
              overflow: hidden;
              img {
                height: 100%;
                width:pxToRem(110px);
                height:pxToRem(60px);
              }
            }
            .ttl {
              flex: 1;
              font-size: pxToRem(36px);
              color: $color-text;
              font-weight: 600;
              text-align: left;
            }
          }

          .dl {
            font-size: pxToRem(26px);
            margin-bottom:pxToRem(20px);
            line-height: pxToRem(30px);
            color: $color-text;
          }
        }
      }
    }
  }

</style>
