<template>
  <div class="my-order">
    <order-tab @changeTab="handleChangeTab"></order-tab>
    <div class="order-list"  v-if="orderTabCur===0">
      <scroll ref="scroll" class="order-wrapper" :data="unpayLists"
              :beforeScroll="beforeScroll"
              :pullup="pullup"
              @scrollToEnd="searchMore">
        <ul>
          <li v-for="(item,index) in unpayLists" :key="index">
            <base-order :item='item'></base-order>
          </li>
          <loading v-show="hasMore" title=""></loading>
        </ul>

      </scroll>
      <div v-show="!unpayLists.length" class="no-result-wrapper">
        <no-result :title="''" :src="src"
                   :absTop="82"></no-result>
      </div>
    </div>
    <div class="order-list"  v-if="orderTabCur===1">
      <scroll ref="scroll" class="order-wrapper" :data="guranteeLists"
              :beforeScroll="beforeScroll"
              :pullup="pullup"
              @scrollToEnd="searchMore">
        <ul>
          <li v-for="(item,index) in guranteeLists" :key="index">
            <base-order :item='item'></base-order>
          </li>
          <loading v-show="hasMore" title=""></loading>
        </ul>

      </scroll>
      <div v-show="!guranteeLists.length" class="no-result-wrapper">
        <no-result :title="''" :src="src"
                   :absTop="82"></no-result>
      </div>
    </div>
    <div class="order-list"  v-if="orderTabCur===2">
      <scroll ref="scroll" class="order-wrapper" :data="orderlists"
              :beforeScroll="beforeScroll"
              :pullup="pullup"
              @scrollToEnd="searchMore">
        <ul>
          <li v-for="(item,index) in orderlists" :key="index">
            <base-order :item='item'></base-order>
          </li>
          <loading v-show="hasMore" title=""></loading>
        </ul>

      </scroll>
      <div v-show="!orderlists.length" class="no-result-wrapper">
        <no-result :title="''" :src="src"
                   :absTop="82"></no-result>
      </div>
    </div>
    <!--<orderList :lists="unpayLists"  v-if="orderTabCur===0"></orderList>-->
    <!--<orderList :lists="guranteeLists"  v-if="orderTabCur===1"></orderList>-->
    <!--<orderList :lists="orderlists" v-if="orderTabCur===2"></orderList>-->
    <!--<div class="order-list-wrappwer">-->
    <!--<scroll ref="scroll" class="order-wrapper" :data="unpayLists"-->
    <!--:beforeScroll="beforeScroll"-->
    <!--:pullup="pullup"-->
    <!--@scrollToEnd="searchMore">-->
    <!--<ul>-->
    <!--<li v-for="(item,index) in unpayLists" :key="index">-->
    <!--<base-order :item='item'></base-order>-->
    <!--</li>-->
    <!--<loading v-show="hasMore" title=""></loading>-->
    <!--</ul>-->

    <!--</scroll>-->
    <!--<div v-show="!unpayLists.length" class="no-result-wrapper">-->
    <!--<no-result :title="''" :src="src"-->
    <!--:absTop="82"></no-result>-->
    <!--</div>-->
    <!--</div>-->

    <router-view></router-view>
  </div>
</template>

<script>
import scroll from '@/components/base/scroll'
import orderTab from '@/components/order/myOrderTab'
// import orderList from '@/components/order/BaseOrderList'
import baseOrder from '@/components/order/BaseOrder'
import noResult from '@/components/base/noResult'
import loading from '@/components/base/loading'
import {
  mapState,
  mapGetters,
  mapMutations
} from 'vuex'

export default {
  name: 'myOrder',
  data () {
    return {
      beforeScroll: true,
      pullup: true,
      hasMore: true,
      src: require('@/assets/images/no_order.png'),
      page: 1,
      limit: 10,
      status: '4',
      orderlists: [],
      unpayLists: [],
      guranteeLists: []
      // tabs: [
      //   {
      //     tabtxt: '',
      //     active: false,
      //     status: '0'
      //   },
      //   {
      //     tabtxt: '',
      //     active: true,
      //     status: '4'
      //   },
      //   {
      //     tabtxt: '',
      //     active: false,
      //     status: ''
      //   }
      // ]
    }
  },
  mounted () {
    this.getOrderList()
  },
  computed: {
    ...mapState([
      'orderTabCur',
      'orderTabCurState'
    ])

  },
  methods: {
    ...mapMutations(['SET_ORDER_TAB_CUR']),
    handleChangeTab (index) {
      this.getOrderList()
    },
    //   this.getOrderList()
    // },
    getOrderList () {
      this.page = 1
      let postData = {
        page: this.page,
        limit: this.limit,
        status: this.orderTabCurState
      }
      this.$api.personCenter.getIndexOrderList(postData).then((res) => {
        if (res.code === 1) {
          // this.lists = res.data.dataGrid.data
          switch (this.orderTabCurState) {
            case '0':
              this.unpayLists = res.data.dataGrid.data
              break
            case '4':
              this.guranteeLists = res.data.dataGrid.data
              break
            case '':
              this.orderlists = res.data.dataGrid.data
              break
          }
          this._checkMore(res.data)
          // $set(this.lists, `status${this.status}`, res.data.dataGrid.data)

        }
      })
    },
    searchMore () {
      console.log('searchmore',this.hasMore)
      if (!this.hasMore) {
        return
      }

      this.page++
      this.$api.personCenter.getIndexOrderList({
        page: this.page,
        limit: this.limit,
        status: this.orderTabCurState
        // status: this.status
      }).then(
        function (res) {
          // this.$set(this.lists, `status${this.status}`, res.data.dataGrid.data)
          switch (this.orderTabCurState) {
            case '0':
              this.unpayLists = this.unpayLists.concat(res.data.dataGrid.data)
              break
            case '4':
              this.guranteeLists = this.guranteeLists.concat(res.data.dataGrid.data)
              break
            case '':
              this.orderlists = this.orderlists.concat(res.data.dataGrid.data)
              break
          }
          this._checkMore(res.data)

        }.bind(this)
      )
        .catch(function (err) {
        })
    },
    _checkMore (data) {
      console.log('hasMore')
      if (!data.dataGrid.data.length || (this.page * this.limit) >= data.total) {
        console.log('nohasMore')
        this.hasMore = false
      }
    }
  },
  components: {
    noResult,
    scroll,
    baseOrder,

    orderTab,
    // orderList
    loading
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .my-order {
    background: #f7f7f7;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin-top: pxToRem(84px);
    .order-list{
      height:100%;
      overflow: hidden;
    }
    .order-wrapper {
      height: calc(100% - 0.82rem);
      overflow: hidden;
    }
  }

</style>
